@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
body {
  margin: 0;
  padding: 0;
    font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100vh;
}

.App{
  color: rgba(189, 189, 192, 1);
  display: flex;
  height: 100%;
}


.Sidebar{
    display: flex;
    width:25%;
    flex-direction: column;
    height: 100vh;
    background-color: rgba(25, 25, 27, 1);
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
    border-right: 1px solid rgba(189, 189, 192, 0.1);
}

.user-section-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 20%;
    padding: 0.7em;
}

#main-user{
    padding: 0.5rem;
}

.user-pic {
    width: 3em;
    border-radius:50%; 
}

#main-user-search{
    align-self: center;
    height: 2em;
    width: 90%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
}

.sidebar-chats{
    display:flex;
    flex-direction: column;
    border-top: 1px solid rgba(189, 189, 192, 0.2);
    overflow-y: scroll;
    padding-top:2em;
}


.user-pic:hover {
    cursor: pointer;
}



.User {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
}
.User:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.User__pic {
    width: 50px;
    border-radius: 50%;
}

.User__details {
    display: none;
}

/* not small devices  */
@media (min-width: 600px) {
    .User__details {
        display: block;
        padding: 0 0 0 0.8rem;
    }
    .User__details-name {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1.2rem;
    }

    .User__details-status {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1rem;
        font-weight: lighter;
    }
}
.Main {
    flex: 1 1;
    background-color: rgba(25, 25, 27, 1);
    height: 100%;
}
.Empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.Empty__name {
    color: #fff;
    font-weight: lighter;
}
.Empty__status,
.Empty__info {
    padding: 1rem;
}
.Empty__status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}
.Empty__img {
    border-radius: 50%;
    margin: 1rem 0;
}
.Empty__btn {
    padding: 0.8rem;
    margin: 1rem 0;
    font-weight: lighter;
    font-size: 1rem;
    border-radius: 20px;
}
.Empty__btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
}
.Header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.7em;
    border-bottom: 1px solid rgba(189, 189, 192, 0.2);

}
.Header__name {
    color: #fff;
    font-weight: lighter;
}

.Header_status{
    width: 50%;
    height: auto;
    font-weight: lighter;
}

@media screen and (max-width: 400px){
    .Header{
        padding: 0.5em;
    }
    .Header__name{
        font-weight: bolder;
        font-size: 1.5rem;
    }

    .Header_status{
        font-size: 0.5rem;
    }
}
.Chats {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin: 0 auto;
    overflow-y: scroll;
    
}


.Chat {
    margin: 1.5rem ;
    font-weight: lighter;
    color: #fff;
    padding:0.5rem;
    background: -webkit-gradient(linear, left top, right top, from(#1986d8), to(#7b9cc2));
    background: -webkit-linear-gradient(left, #1986d8, #7b9cc2);
    background: linear-gradient(90deg, #1986d8, #7b9cc2);
    max-width: 100%;
    border-radius: 10px;
    display: flex;
}

@media screen and (max-width: 400px){
    .Chat{
        font-size: 0.8rem;
    }
}

.Chat.is-user-msg {
    margin-left: auto;
    background: #2b2c33;
    border-radius: 10px;
}

.chat-menu-icon{
    margin-left: 0.5rem;
    font-size: 1.4rem;
    margin-top: -0.6rem;
}

.hide{
    visibility: hidden;
}


@media (min-width: 400px) {
    .Chat {
        max-width: 60%;
    }
}

#message_contaier{
    display: flex;
    justify-content: space-between;
}

#message_container_inner{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.list-group-item:hover{
    cursor: pointer;
}
.ChatWindow {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

@media screen and (max-width: 400px){
    .ChatWindow{
        align-items: center;
        justify-content: center;
    }
}
.Message {
    width: 80%;
    margin: 1rem auto;
}
.Message__input {
    width: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
}

@media screen and (max-width: 400px){
    .Message__input{
        width: 80%;
        align-self: center;
        font-size: 0.8rem;
    }
}
.App {
  display: flex;
}




