
.User {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
}
.User:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.User__pic {
    width: 50px;
    border-radius: 50%;
}

.User__details {
    display: none;
}

/* not small devices  */
@media (min-width: 600px) {
    .User__details {
        display: block;
        padding: 0 0 0 0.8rem;
    }
    .User__details-name {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1.2rem;
    }

    .User__details-status {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1rem;
        font-weight: lighter;
    }
}